.wrapper {
    display: flex;
    flex-direction: column;
    gap: 15px;
    padding: 15px;
    text-align: center;
  }
  
  .text {
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    font-size: 20px;
  }
  
  .pre_text {
    margin: 20px 0;
  }
  
  .link {
    display: inline-block;
    text-decoration: none;
    padding: 10px 25px;
    font-size: 22px;
    font-weight: 500;
    border-radius: 5px;
    outline: none;
    border: none;
    cursor: pointer;
    transition: all 0.3s ease 0s;
    color: rgb(255, 255, 255);
    background-color: rgb(69 189 69);
  }
  
  .link:hover,
  .link:focus {
    background-color: #28ab28;
    box-shadow: 0px 0px 8px 0px rgba(34, 60, 80, 0.2);
  }